@import url('https://fonts.googleapis.com/css2?family=Chivo+Mono:wght@500&display=swap');
.header{
    height: 60px;
    width: 100vw;
    padding: 20px 10vw 0px 15vw;
    box-sizing: border-box;
    background-color: #FFFFFF;
}
.navigation{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.Logo,.login{
    font-size: 26px;
    font-family: 'Chivo Mono', monospace;
    color: #3C4852;
}
.login{
    padding: 5px;
    height: 35px;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 35px;
    border-radius: 50%;
    overflow: hidden;
}