.option {
    display: flex;
    padding-left: 32px;
    margin: 12px 0px 12px 0px;
}

.Onum {
    font-weight: 400;
    font-size: 29px;
    line-height: 61px;
    color: #000000;
    padding-right: 20px;
}

.OtextArea {
    background: #FFFFFF;
    border-radius: 5px;
    padding-left: 10px;
    width: 100%;
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.Otext {
    font-weight: 400;
    font-size: 23px;
    line-height: 46px;
    color: #7F8086;
}
.controls{
    height: 60px;
    width:100px;
    display: flex;
}
.controls span {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 46px;
    font-size: 28px;
    height: 100%;

}
.controls span{
    color: #47BCA4;
    /* background: #EAFAF6; */
}
.controls span:last-child{
    color: #e82121;
}