.formArea {
  width: 100%;
  margin-bottom: 20px;
}

.formControl {
  font-size: 25px;
  width: calc(100%);
  border: #3c485245 solid 1px;
  padding: 5px 10px;
  margin: auto;
  border-radius: 10px;
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  box-sizing: border-box;
}

.formControl input {
  height: 40px;
  border: none;
  font-size: 19px;
  background-color: transparent;
  width: 70%;
  margin-top: 10px;
}

.formControl input::placeholder {
  color: #828384;
  letter-spacing: 1px;
}

.formControl input:focus {
  outline: none;
}

.textarea {
  resize: none;
  height: auto;
  outline: none;
  border: none;
  width: 100%;
  padding: 5px;
  font-size: 1.2rem;
  overflow: hidden;
  overflow-y: auto;
  box-sizing: border-box;
}

.textarea::placeholder {
  font-size: 1.2rem;
}
