.AddQuestion{
    width: '100vw' !important;
}
.questionsArea {
    position: relative;
    width: 100%;
    top: 120px;
    height: calc(100vh - 120px);
    margin: auto;
    width: 70%;
    overflow-y: scroll;
    /* border: red solid 1px; */
}

.addBtn {
    font-weight: 700;
    font-size: 24px;
    line-height: 61px;
    border: none;
    color: #4C84EA;
    background: #EAF0FD;
    border-radius: 5px;
    width: 100%;
}

.modalBackground {
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 20;
    position: absolute;
    top: 0;
    left: 0;
}

.modal {
    width: 35vw;
    height: 60vh;
    background-color: #FFFFFF;
    padding-left: 20px;
    z-index: 40;
    overflow-y: scroll;
    border-radius: 10px;
}

.closeModal {
    position: sticky;
    font-size: 2rem;
    margin: 20px 0px 10px 0px;
    box-sizing: border-box;
    color: #3c4852;
}