.formArea {
    width: 100%;
    margin-bottom: 20px;
}

.formControl {
    font-size: 25px;
    width: calc(100%);
    border: #3c485245 solid 1px;
    padding: 5px 10px;
    margin: auto;
    border-radius: 10px;
    margin-bottom: 20px;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    box-sizing: border-box;
}

.formControl input, .formControl select {
    height: 40px;
    border: none;
    font-size: 19px;
    background-color: transparent;
    width: fit-content;
    margin-top: 10px;
}
.formControl input[type='text']{
    width: 70%;
}
.formControl select option{
    padding: 4px;
}

.formControl input::placeholder {
    color: #828384;
    letter-spacing: 1px;
}

.formControl input:focus,.formControl select:focus {
    outline: none;
}