.modalBackground {
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 20;
    position: absolute;
    top: 0;
    left: 0;
}

.modal {
    width: 35vw;
    height: 60vh;
    background-color: #FFFFFF;
    padding-left: 20px;
    z-index: 40;
    overflow-y: scroll;
    border-radius: 10px;
}

.closeModal {
    position: sticky;
    font-size: 2rem;
    margin: 20px 0px 10px 0px;
    box-sizing: border-box;
    color: #3c4852;
}