.CreateTest {
    background: #FCFCFC;
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

.ContentWrapper {
    width: calc(100vw - 10vw);
    height: calc(100vh - 10vh);
    background: #FFFFFF;
    box-shadow: 0px 13px 32px 5px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    position: relative;
}

.TestName {
    width: auto;
    height: 84px;
    font-style: normal;
    font-weight: 600;
    font-size: 50px;
    line-height: 84px;
    /* identical to box height */
    color: #3c4852;
}

.Details {
    position: absolute;
    top: 22%;
    left: 58px;
}

.Test {
    margin-bottom: 10rem;
}

.img {
    height: 500px;
    position: absolute;
    right: 25px;
    top: 15%;
}

.detailsArea {
    display: flex;
    /* justify-content: space-between; */
    align-items: center;
    flex-wrap: wrap;
    width: 90%;
    margin-top: 32px;
}

.eachDetail {
    display: flex;
    margin: 12px;
}

.detailsPara {
    display: flex;
    flex-direction: column;
    font-size: 22px;
}

.detailsPara div {
    width: auto;
}

.detailsPara div:first-child {
    font-weight: 700;
}

